@use '../base/variables' as v;
@use '../base/mixins' as m;

.nav{
    margin: 0 auto 6rem auto;
    
}
@include m.mq(768px) {
    .nav{
        margin: 0 ;
        
    }
}
@include m.mq(1440px) {
    .nav{
        margin-right: 10rem;
    }
}
.nav__lista{
    height: 10rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    gap: 1rem;
    padding: 0;
    margin-top: 3rem;
    margin-bottom: 9rem;
    
    .nav__link{
        color: v.$crema;
        margin: 0 ;
        padding: 1rem 2rem;
        border-radius: 1rem;
        text-align: center;
        transition: background-color .5s ease;
    }
    .nav__link:hover{
        background-color: v.$rojo;
        color: v.$crema;
        margin: o auto;
        border: 1px solid v.$crema;
    }
   
}
@include m.mq(768px){
    .nav__lista{

        height: 10rem;
        display: flex;
        flex-direction: row;
        justify-content:center;
        align-items: center;
        gap: 0;  
        margin: 0 auto;
    }
    .nav__item{
        height: 100%;
        display: flex;
        align-items: center;
    }
}
@include m.mq(954px){
    .nav__lista{
        height: 10rem;
        display: flex;
        flex-direction: row;
        justify-content:center;
        align-items: center;
        gap: 2rem;  
        margin: 0 auto;
    }
}
