@use '../base/variables' as v;
@use '../base/mixins' as m;

.trabajamos{
    background-color: v.$crema;
    height: 100vh;
    margin: 5rem 0 5rem 0;
    padding: 10rem 0;
}
.trabajamos__grid{
    margin: 25% 0;
}
@include m.mq(768px) {
    .trabajamos__grid{
        margin: 0;
    }
}
.trabajamos__textos{
    display: grid;
    width: 100%;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2,1fr);
}
.trabajamos__heading{
    font-size: 5rem;
    font-weight: bolder;
    text-align: center;
    grid-column: 1 / 4 ;  
    grid-row: 1 /2;
    margin: 0 auto;
    padding:  0;
}
.trabajamos__heading3{
    font-size: 1.5rem;
    font-weight: bolder;
    margin: auto auto;
    transition: all 1s;
    text-align: center;
}
.trabajamos__heading3:hover{
    font-size: 2.5rem;
    background-color: v.$rojo;
    color: v.$crema;
    padding: 2rem 1rem;
    font-weight: bolder;
    margin: auto auto;

    &::after{
        content: '';
        background-color: v.$crema;
        height: 1px;
        display: flex;
        flex-direction: column;
        margin: 1rem;
        transition: all 1s ease-in-out;
    }
}
@include m.mq(768px) {
    .trabajamos__heading{
        font-size: 5rem;
        font-weight: bolder;
        grid-column: 1 / 4 ;  
        grid-row: 1 /2;
        margin: 0 auto;
        padding: 5rem 0;
    }
    .trabajamos__heading3{
        font-size: 3.5rem;
        font-weight: bolder;
        margin: auto auto;
        transition: all 1s;
    }
    .trabajamos__heading3:hover{
        font-size: 4rem;
        background-color: v.$rojo;
        color: v.$crema;
        padding: 2rem 1rem;
        font-weight: bolder;
        margin: auto auto;
    
        &::after{
            content: '';
            background-color: v.$crema;
            height: 1px;
            display: flex;
            flex-direction: column;
            margin: 1rem;
            transition: all 1s ease-in-out;
        }
    } 
}

.trabajamos__video{
    box-shadow: 5px 5px 40px 30px rgba(255, 255, 255, 0.7), inset 0px 0px 0px 46px rgba(255, 255, 255, 0.64);
    width:100%;
    height: 100%;
    background-color: v.$blanco;
    margin: 0 auto;
    
}
@include m.mq(769px) {
    .trabajamos__video{
        box-shadow: 5px 5px 40px 30px rgba(255, 255, 255, 0.7), inset 0px 0px 0px 46px rgba(255, 255, 255, 0.64);
        width:100rem;
        height: 50rem;
        background-color: v.$blanco;
        margin: 0 auto;
        
    }
}
.video{
    width: 100%;
    height: 100%;
    display: block;
    margin: 0 auto;
    
}