@use 'variables'as v;

html {
    box-sizing: border-box;
    font-size: 62.5%;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    margin: 0;
    font-family: v.$f-principal;
    font-size: 1.6rem;
}

img {
    width: 100%;
    display: block;
}
picture{
    all:unset;
    width: auto;
    height: auto;
    display: inline-block;
}
a{
    text-decoration: none;
    font-family: v.$f-h2;
}
li{
    list-style: none;
}
h1,
h2,
h3,
.header__texto{
    color: v.$rojo;
}
p{
    font-family: v.$f-p;
}
[class$="__contenedor"]{
    max-width: 120rem;
    margin: 0 auto;
}
[class$="__capa"]{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(247, 246, 242, 0.4);
    mix-blend-mode: overlay;
}