@use '../base/variables'as v;
@use '../base/mixins'as m;

.servicios {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: v.$rojo;

    .servicios__heading {
        text-align: center;
        font-size: 3rem;
        font-weight: bolder;
        margin: 10rem 0;
    }
}

.servicio {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

}

.servicio__item {
    padding: 2rem;
    margin: 0 2rem 2rem 2rem;
    max-width: 65rem;
    height: 100%;
    
    background-color: v.$tuquesa;
    transition: background-color .6s ease-in-out, color .6s ease-in-out;
}

.servicio__item:hover {

   background-color: v.$rojo;
   border: 4px dotted rgb(247 245 242);
   & .servicio__heading,
   & .servicio__texto{
    color: v.$crema;
   }
}

.servicios__heading,
.servicio__heading,
.servicio__texto {
    color: v.$crema;
}

.servicio__heading{
    font-size: 3rem;
}
.servicio__heading:AFTER{
    content: '';
    display: flex;
    flex-direction: column;
    height: 1rem;
    margin: 2REM;
    background-color: v.$tuquesa;
}
.servicios__contenido{
    
    margin: 5rem;
}
@include m.mq(992px) {
    .servicios__contenido{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 5rem;
        margin: 10rem;
    }
    .servicio__item{
        margin: 0;
    }
    
    .servicio:nth-child(3){
      grid-column: 1 / 3;
      margin: 0 auto;  
    }

}