@font-face {
  font-family: "MasvisHC";
  src: url(../../../Masvis.woff2);
}
@font-face {
  font-family: "MasvisHCi";
  src: url(../../../MasvisItalic.woff2);
}
@font-face {
  font-family: "now";
  src: url(../../../Now-Regular.otf);
}
@font-face {
  font-family: "DMS";
  src: url(../../../DMSerifDisplay-Regular.ttf);
}
@font-face {
  font-family: "DMSi";
  src: url(../../../DMSerifDisplay-Italic.ttf);
}
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "MasvisHC", sans-serif;
  font-size: 1.6rem;
}

img {
  width: 100%;
  display: block;
}

picture {
  all: unset;
  width: auto;
  height: auto;
  display: inline-block;
}

a {
  text-decoration: none;
  font-family: "DMS", sans-serif;
}

li {
  list-style: none;
}

h1,
h2,
h3,
.header__texto {
  color: #A9143B;
}

p {
  font-family: "now", sans-serif;
}

[class$=__contenedor] {
  max-width: 120rem;
  margin: 0 auto;
}

[class$=__capa] {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(247, 246, 242, 0.4);
  mix-blend-mode: overlay;
}

.header {
  min-height: 100vh;
  position: relative;
}

.header__contenedor {
  height: 100%;
}

.header__videoFondo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header__contenido {
  position: relative;
  width: 100%;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 20%;
}
.header__contenido .header__heading {
  font-size: 5rem;
  font-weight: 700;
  margin: 0;
  line-height: 0.8;
  transition: all 0.4s ease-in;
}
.header__contenido .header__heading2 {
  margin: 0;
  font-size: 2rem;
  line-height: 0;
  letter-spacing: 1px;
  font-weight: 300;
}
.header__contenido .header__texto {
  max-width: 60%;
  margin-top: 5rem;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
  letter-spacing: 2px;
}
.header__contenido .header__heading3 {
  font-size: 2rem;
  margin-top: 4rem;
  letter-spacing: 2px;
}
@media (min-width: 768px) {
  .header__contenido .header__heading {
    font-size: 12rem;
  }
  .header__contenido .header__heading:hover {
    font-size: 13rem;
  }
  .header__contenido .header__texto {
    max-width: 90%;
    margin-top: 12rem;
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 2px;
  }
  .header__contenido .header__heading3 {
    font-size: 4.7rem;
    margin-top: 12rem;
  }
}
@media (min-width: 992px) {
  .header__contenido .header__heading {
    font-size: 15rem;
    font-weight: 700;
    margin: 0;
    line-height: 0.8;
    transition: all 0.4s ease-in;
  }
  .header__contenido .header__heading:hover {
    font-size: 17rem;
    padding: 2rem 0;
    border-radius: 10px;
    box-shadow: 0 0 15px 12px rgba(247, 246, 242, 0.5);
    background: rgba(247, 246, 242, 0.5);
  }
  .header__contenido .header__heading2 {
    margin: 0;
    line-height: 0;
    letter-spacing: 2.2rem;
    font-weight: 300;
  }
  .header__contenido .header__texto {
    max-width: 70%;
    margin-top: 12rem;
    text-align: center;
    font-size: 2rem;
    font-weight: 700;
    letter-spacing: 2px;
  }
  .header__contenido .header__heading3 {
    font-size: 4.7rem;
    margin-top: 12rem;
  }
}

.header__heading:hover {
  font-size: 7rem;
  padding: 1rem 0;
  border-radius: 10px;
  box-shadow: 0 0 15px 12px rgba(247, 246, 242, 0.5);
  background: rgba(247, 246, 242, 0.5);
}

.header__barra {
  position: relative;
  width: 100%;
  height: 28rem;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: rgba(247, 246, 242, 0.1);
  backdrop-filter: blur(10px);
  box-shadow: 0px 0px 15px 5px rgba(42, 41, 40, 0.5);
}

@media (min-width: 768px) {
  .header__barra {
    position: relative;
    width: 100%;
    height: 14rem;
    z-index: 2;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}
.header__logo {
  max-width: 20rem;
  max-height: 20rem;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .header__logo {
    max-width: 30rem;
    max-height: 30rem;
    margin: 0;
  }
}
.header__logoImagen {
  width: 100%;
  height: 100%;
}

.nav {
  margin: 0 auto 6rem auto;
}

@media (min-width: 768px) {
  .nav {
    margin: 0;
  }
}
@media (min-width: 1440px) {
  .nav {
    margin-right: 10rem;
  }
}
.nav__lista {
  height: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 0;
  margin-top: 3rem;
  margin-bottom: 9rem;
}
.nav__lista .nav__link {
  color: #F7F5DD;
  margin: 0;
  padding: 1rem 2rem;
  border-radius: 1rem;
  text-align: center;
  transition: background-color 0.5s ease;
}
.nav__lista .nav__link:hover {
  background-color: #A9143B;
  color: #F7F5DD;
  margin: o auto;
  border: 1px solid #F7F5DD;
}

@media (min-width: 768px) {
  .nav__lista {
    height: 10rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 0;
    margin: 0 auto;
  }
  .nav__item {
    height: 100%;
    display: flex;
    align-items: center;
  }
}
@media (min-width: 954px) {
  .nav__lista {
    height: 10rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    margin: 0 auto;
  }
}
.trabajamos {
  background-color: #F7F5DD;
  height: 100vh;
  margin: 5rem 0 5rem 0;
  padding: 10rem 0;
}

.trabajamos__grid {
  margin: 25% 0;
}

@media (min-width: 768px) {
  .trabajamos__grid {
    margin: 0;
  }
}
.trabajamos__textos {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
}

.trabajamos__heading {
  font-size: 5rem;
  font-weight: bolder;
  text-align: center;
  grid-column: 1/4;
  grid-row: 1/2;
  margin: 0 auto;
  padding: 0;
}

.trabajamos__heading3 {
  font-size: 1.5rem;
  font-weight: bolder;
  margin: auto auto;
  transition: all 1s;
  text-align: center;
}

.trabajamos__heading3:hover {
  font-size: 2.5rem;
  background-color: #A9143B;
  color: #F7F5DD;
  padding: 2rem 1rem;
  font-weight: bolder;
  margin: auto auto;
}
.trabajamos__heading3:hover::after {
  content: "";
  background-color: #F7F5DD;
  height: 1px;
  display: flex;
  flex-direction: column;
  margin: 1rem;
  transition: all 1s ease-in-out;
}

@media (min-width: 768px) {
  .trabajamos__heading {
    font-size: 5rem;
    font-weight: bolder;
    grid-column: 1/4;
    grid-row: 1/2;
    margin: 0 auto;
    padding: 5rem 0;
  }
  .trabajamos__heading3 {
    font-size: 3.5rem;
    font-weight: bolder;
    margin: auto auto;
    transition: all 1s;
  }
  .trabajamos__heading3:hover {
    font-size: 4rem;
    background-color: #A9143B;
    color: #F7F5DD;
    padding: 2rem 1rem;
    font-weight: bolder;
    margin: auto auto;
  }
  .trabajamos__heading3:hover::after {
    content: "";
    background-color: #F7F5DD;
    height: 1px;
    display: flex;
    flex-direction: column;
    margin: 1rem;
    transition: all 1s ease-in-out;
  }
}
.trabajamos__video {
  box-shadow: 5px 5px 40px 30px rgba(255, 255, 255, 0.7), inset 0px 0px 0px 46px rgba(255, 255, 255, 0.64);
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  margin: 0 auto;
}

@media (min-width: 769px) {
  .trabajamos__video {
    box-shadow: 5px 5px 40px 30px rgba(255, 255, 255, 0.7), inset 0px 0px 0px 46px rgba(255, 255, 255, 0.64);
    width: 100rem;
    height: 50rem;
    background-color: #ffffff;
    margin: 0 auto;
  }
}
.video {
  width: 100%;
  height: 100%;
  display: block;
  margin: 0 auto;
}

.servicios {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #A9143B;
}
.servicios .servicios__heading {
  text-align: center;
  font-size: 3rem;
  font-weight: bolder;
  margin: 10rem 0;
}

.servicio {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.servicio__item {
  padding: 2rem;
  margin: 0 2rem 2rem 2rem;
  max-width: 65rem;
  height: 100%;
  background-color: #5BBAB5;
  transition: background-color 0.6s ease-in-out, color 0.6s ease-in-out;
}

.servicio__item:hover {
  background-color: #A9143B;
  border: 4px dotted rgb(247, 245, 242);
}
.servicio__item:hover .servicio__heading, .servicio__item:hover .servicio__texto {
  color: #F7F5DD;
}

.servicios__heading,
.servicio__heading,
.servicio__texto {
  color: #F7F5DD;
}

.servicio__heading {
  font-size: 3rem;
}

.servicio__heading:AFTER {
  content: "";
  display: flex;
  flex-direction: column;
  height: 1rem;
  margin: 2REM;
  background-color: #5BBAB5;
}

.servicios__contenido {
  margin: 5rem;
}

@media (min-width: 992px) {
  .servicios__contenido {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    margin: 10rem;
  }
  .servicio__item {
    margin: 0;
  }
  .servicio:nth-child(3) {
    grid-column: 1/3;
    margin: 0 auto;
  }
}